/* ---------------------------------------------- /*
 * Portfolio
/* ---------------------------------------------- */

.portfolio-item {
	margin: 20px;
}

.portfolio-item-preview {
	position: relative;
}


.portfolio-item:hover .portfolio-item-description {
	opacity: 1;
	bottom: 0;
}

.portfolio-item-description {
	background: rgba(255, 255, 255, .85);
   position: absolute;
   overflow: auto;
	right: 0;
   bottom: 0px;
   max-width: 100%;
   width: 100%;
	left: 0;
	text-align: center;
	opacity: 0;
	color: #222;
}

.dHLACw{
   overflow: visible !important;
}
.contact-photo{
   background-size: contain;
   height: 450px;
   width: 25vw;
}

@media screen and (max-width: 600px){
   .contact-photo{
      height: 60vh;
      width: 80vw;
   }
}

